.row{
margin-left: 20px;
color: white;
}
.row_posters{
display: flex;
overflow: hidden;
overflow-x: scroll;
padding: 20px;
}

.row_posters::-webkit-scrollbar {
 display: none; 

}
.row_poster{
object-fit: contain;
width: 100%;
max-height: 180px;
margin-right: 10px;
transition:transform 250ms;

}

.row_poster:hover{
transform: scale(1.32);

}
.row_posterLarger{

max-height: 270px;
}
.row_posterLarger:hover {
 transform: scale(1.52);

}